  background-position: left top;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  overflow: hidden; }

div#btnProximo {
  font-size: 30px;
  margin: 10px;
  cursor: pointer; }

.btn-fechar {
  border-color: #f6f6f600;
  text-transform: uppercase; }
  .btn-fechar:hover, .btn-fechar:focus, .btn-fechar:active {
    background: transparent;
    color: white;
    border-color: white; }

.btn-produto {
  position: absolute;
  font-size: 3em;
  color: #fff; }
  .btn-produto:hover {
    color: white; }
  .btn-produto::after {
    position: absolute;
    content: '';
    top: 35px;
    left: 25px;
    height: 0;
    width: 0;
    border-radius: 50%;
    border: 4px dotted rgba(255, 255, 255, 0);
    opacity: 0;
    transition: 0.3s; }
  .btn-produto:hover::after {
    top: -14px;
    left: -24px;
    height: 100px;
    width: 100px;
    opacity: 1;
    border: 7px dotted #fff;
    -webkit-animation: spin 3s linear infinite;
    animation: spin 3s linear infinite; }

.floating-buttons {
  width: 100vw;
  position: fixed;
  left: 0;
  cursor: pointer; }
  .floating-buttons.top .buttons-right {
    top: 0; }
  .floating-buttons.top .buttons-left {
    top: 0; }
  .floating-buttons.bottom .buttons-right {
    bottom: 46px; }
  .floating-buttons.bottom .buttons-left {
    bottom: 46px; }
  .floating-buttons .buttons-left {
    position: absolute;
    margin: 15px 30px;
    left: 0;
    font-size: 2.5em; }
    .floating-buttons .buttons-left span {
      color: #ffffff;
      text-decoration: none;
      margin-right: 20px;
      transition: color 0.2s; }
      .floating-buttons .buttons-left span:hover {
        color: #aeaeae; }
  .floating-buttons .buttons-right {
    position: absolute;
    margin: 15px 30px;
    right: 0;
    font-size: 2.5em; }
    .floating-buttons .buttons-right span {
      color: #ffffff;
      text-decoration: none;
      margin-left: 25px;
      transition: color 0.2s; }
      .floating-buttons .buttons-right span:hover {
        color: #aeaeae; }

#Modal {
  z-index: 3;
  position: fixed;
  top: 0;
  height: 100%;
  width: 100vw; }

#ListaDeProdutos {
  margin: 0 auto;
  padding: 25px 100px;
  width: 100%; }
  #ListaDeProdutos hr {
    width: 65%; }

.modal-container {
  height: calc(100% - 6rem);
  max-width: 800px;
  margin: 3rem auto; }

.modal-content {
  height: 100%; }

.modal-items-container {
  overflow: auto; }

.sidebar-btn-close {
  font-size: 30px;
  position: absolute;
  top: 15px;
  right: 16px !important;
  color: #1b1b1b;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: 0.5s;
  text-decoration: none; }
  .sidebar-btn-close:hover {
    background: #dadada;
    text-decoration: none;
    color: #1b1b1b; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg); } }

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.sidebar-container {
  max-width: 500px;
  padding: 25% 40px 40px 40px; }

.sidebar-btn-close {
