.button-primary {
  background-color: #1b1b1b;
  border: 1px solid #1b1b1b;
  border-radius: 0;
  text-transform: uppercase;
  padding: 10px 20px;
  color: white !important;
  transition: background-color .2s;
  cursor: pointer; }
  .button-primary:hover {
    background-color: #38383800;
    color: #1b1b1b !important; }

.button-arrow svg {
  position: relative;
  font-size: 30px; }

.counter {
  display: flex;
  justify-content: center;
  align-items: center; }
  .counter-value {
    font-size: 25px;
    margin: 0 15px; }
  .counter-box {
    background: black;
    color: white;
    height: 20px;
    width: 20px;
    cursor: pointer; }
    .counter-box-minus {
      position: relative; }
      .counter-box-minus:after {
        content: '-';
        position: absolute;
        top: -2px;
        left: 7px; }
    .counter-box-plus {
      position: relative; }
      .counter-box-plus:after {
        content: '+';
        position: absolute;
        top: -2px;
        left: 5px; }

.carrinho-container {
  max-width: 500px;
  padding: 40% 40px 40px;
  text-align: left; }

.carrinho-btn-close {
  font-size: 30px;
  position: absolute;
  top: 15px;
  left: 16px;
  color: #1b1b1b;
  background: white;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: .5s; }
  .carrinho-btn-close:hover {
    background: #dadada;
    text-decoration: none;
    color: #1b1b1b; }

.carrinho-card {
  text-align: left;
  display: flex;
  align-items: center;
  margin-bottom: 50px; }
  .carrinho-card-info {
    padding-left: 20px; }
    .carrinho-card-info h5,
    .carrinho-card-info p {
      margin-bottom: 0; }
  .carrinho-card-img img {
    width: 100px; }

.carrinho-checkout {
  display: flex;
  align-items: flex-end;
  justify-content: space-between; }
  .carrinho-checkout p {
    font-size: 20px;
    text-transform: uppercase; }

@media screen and (max-width: 500px) {
  .carrinho-container {
    max-width: 320px; }
  .carrinho-checkout {
    align-items: center;
    flex-flow: column; }
    .carrinho-checkout p {
      padding-top: 10px; } }

.toastr-produto {
  color: #212121;
  padding: 15px 15px 15px 15px;
  text-transform: uppercase; }
  .toastr-produto-progress {
